body {
  margin: 0;
  padding: 0;
  background: url(/react-formValidation/static/media/bg.49247ab5.jpg) no-repeat center;
  width: 100%;
  min-height: 100vh;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
form {
    max-width: 800px;
    padding: 30px;
    margin: 0 auto;
    text-align: center;
}

label {
    display: flex;
    flex-direction: column;
    margin: 15px;
    font-size: 20px;
    flex-wrap: wrap;
}

input {
    padding: 10px;
    font-size: 18px;
    max-width: 300px;
    margin: 10px auto;
    border: none;
    box-shadow: 0 0 5px black;
    overflow: auto;
    font-family: sans-serif;
}

input[type="checkbox"] {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    cursor: pointer;
}

.btn {
    padding: 15px 30px;
    text-transform: uppercase;
    font-size: 24px;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    color: white;
    background-color: transparent;
    border: 2px solid white;
    cursor: pointer;
    transition: .3s;
}

@media (min-width: 1024px) {
    .btn:hover {
        background-color: black;
        border: 2px solid black;
        box-shadow: 0 0 5px white;
    }
}
.app {
    width: 100%;
    min-height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    text-align: center;
}

.app__title {
    margin: 0;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.6);
}
.errorMessages {
    text-align: center;
}

.errorMessages__title {
    margin: 0;
    padding: 20px;
    text-shadow: 0 0 5px red;
}


span {
    display: block;
    padding: 10px 15px;
    text-shadow: 0 0 5px red;
}

span:after {
    content: '';
    display: block;
    height: 1px;
    width: 30%;
    margin: 5px auto;
    background-color: rgba(255, 255, 255, 0.2);
}
.sendMessage {
    text-align: center;
    margin: 0;
    padding: 0 20px 30px 20px;
    font-size: 24px;
    text-shadow: 0 0 5px greenyellow;
}
